* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Varela Round", sans-serif; }

html {
  font-size: 10px; }

body {
  height: 100vh;
  width: 100%;
  background: #1ebee6;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff; }

.form__group {
  width: 600px;
  margin-top: 2.2rem;
  display: flex;
  flex-direction: column; }

.form__label {
  font-size: 2.5rem;
  margin: 1rem auto 1rem auto;
  display: block; }

.form__input {
  width: 90%;
  color: #333;
  font-size: 1.6rem;
  margin: 0 auto;
  padding: 1.5rem 2rem;
  background-color: #ffffff;
  border: none;
  display: block; }

.form__items {
  height: 257px;
  overflow: scroll; }

.form__item {
  list-style: none;
  width: 90%;
  color: #333;
  font-size: 1.6rem;
  margin: 0.2rem auto;
  padding: 1.5rem 2rem;
  background-color: #ffffff;
  border: none;
  display: block; }

.form__item:last-child {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px; }

.form__item:hover {
  background-color: #f1505e;
  color: #ffffff; }

.list {
  margin: 25px auto 25px auto;
  height: auto;
  display: flex;
  flex-direction: column; }

.card {
  background-color: #ffffff;
  color: #333333;
  width: 540px;
  height: 100px;
  display: flex;
  justify-content: space-between;
  margin: 5px auto;
  border-radius: 5px; }

.card__body {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.card__btn {
  margin-right: 10px;
  align-self: center;
  width: 40px;
  height: 40px;
  border: none;
  background: url(./static/close.svg) center no-repeat; }

p {
  margin: 3px 0 3px 5px;
  font-size: 1.6rem; }
